// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import VueAwesomeSwiper from 'vue-awesome-swiper'
import VueFlatpickr from 'vue-flatpickr'
// import 'vue-flatpickr/theme/material_green.css'
import './assets/js/fastclick.min'
import './assets/js/scrollfix'
import './assets/js/optimise_tool'

Vue.config.productionTip = false
Vue.use(VueAwesomeSwiper)
Vue.use(VueFlatpickr)

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  template: '<App/>',
  components: { App },
  data: {
    eventHub: new Vue(),
    checkWait: [    // 
      {id: '00011', itemName: '', initiator: '', initiateBank: '', time: '2017-05-15', result: '', tel: '13800001111'},
      {id: '00012', itemName: '', initiator: '', initiateBank: '', time: '2017-05-15', result: '', tel: '13800001111'}
    ],
    approved: [   // 
      {id: '10013', itemName: '', initiator: '', initiateBank: '', time: '2017-05-15', result: '(: )', tel: '13800001111'}
    ],
    organization: [   // 
      {
        id: '0',
        name: '',
        options: [
          {
            id: '00',
            name: '',
            options: [
              {id: '0000', name: ''},
              {id: '0001', name: ''},
              {id: '0002', name: ''},
              {id: '0003', name: ''}
            ]
          },
          {
            id: '01',
            name: '',
            options: [
              {id: '0100', name: ''},
              {id: '0101', name: ''},
              {id: '0102', name: ''}
            ]
          }
        ]
      }
    ],
    effectivity: [    // 
      {id: '001', name: '(_)', type: 1, times: '2017-5-19 9:00 - 2017-5-25 17:00'},
      {id: '002', name: '(_)', type: 0, times: '()'}
    ],
    loseEfficacy: [   // 
      {id: '001', name: '(_)', type: 0, times: '2017-5-11 9:00 - 2017-5-18 17:00'}
    ]
  },
  methods: {
    // 
    flowFun: function (type, id, name) {
      for (let i = 0; i < this.checkWait.length; i++) {
        if (this.checkWait[i].id === id) {
          let item = this.checkWait[i]
          this.checkWait.splice(i, 1)
          switch (type) {
            case '':
              item.result = ''
              this.approved.unshift(item)
              break
            case '':
              item.result = ''
              this.approved.unshift(item)
              break
            case '':
              item.result = '(:' + name + ')'
              this.approved.unshift(item)
              break
            default:
              break
          }
        }
      }
    },
    // 
    cancelAcc: function (id) {
      for (let i=0; i < this.effectivity.length; i++) {
        if (this.effectivity[i].id === id) {
          let item = this.effectivity[i]
          this.effectivity.splice(i, 1)
          item.type = 0
          this.loseEfficacy.unshift(item)
        }
      }
    }
  }
})



