<template>
  <div class="my-check">
    <Head-info :name="headInfo.name" :isBack="headInfo.isBack"
               :iClass="headInfo.iClass" @iconFun="checkFlow"></Head-info>
    <article class="main" :class="{'my-check-main': isBottom}">
      <swiper :options="swiperOption">
        <!--  -->
        <swiper-slide v-for="item in items">
          <p class="title">{{ item.name }}</p>
          <div class="content">
            <template v-if="item.type">
              <template v-for="second in item.item">
                <p class="second-title">{{ second.name }}</p>
                <template v-for="(cont, index) in second.item">
                  <p class="field second-field">
                    <span class="key">{{ index }}:</span>
                    <span class="value">{{ cont }}</span>
                  </p>
                </template>
              </template>
            </template>
            <template v-else="">
              <template v-for="(cont, index) in item.item">
                <p class="field">
                  <span class="key">{{ index }}:</span>
                  <span class="value">{{ cont }}</span>
                </p>
              </template>
            </template>
          </div>
        </swiper-slide>
        <!--  -->
        <div class="swiper-pagination" :class="{'pag-bottom': isBottom}" slot="pagination"></div>
      </swiper>
    </article>
    <footer v-if="isBottom" class="footer">
      <template v-for="item in buttonItem">
        <button-style :buttonName="item" @clickFun="buttonClick(item)"></button-style>
      </template>
    </footer>
    <!---->
    <!--<div v-show="shadowShow" class="shadow"></div>-->
    <div v-show="shadowShow" class="shadow" @click="closePopup"></div>
    <hint-popup v-show="isHintPopup" :hintName="popupName" :buttons="popupButtons"
                :itemName="itemName" :item="baseItem" @hintClick="hintFun"
                :type="popupType"></hint-popup>
    <choose-popup v-show="isChoosePopup" :popupName="popupName" :type="popupType" :buttons="popupButtons"
                  @popupClick="comFun"></choose-popup>
    <com-popup v-show="isComPopup" popupName="" itemName="" type="" :buttons="popupButtons"
               @comClick="comFun"></com-popup>
  </div>
</template>

<script>
  import HeadInfo from '@/components/head'
  import { swiper, swiperSlide } from 'vue-awesome-swiper'
  import ButtonStyle from '@/components/buttonStyle'
  import HintPopup from '@/components/hint-popup'
  import ChoosePopup from '@/components/choose-popup'
  import ComPopup from '@/components/commission-popup'

  export default{
    name: 'my-check',
    components: {
      HeadInfo,
      swiper,
      swiperSlide,
      ButtonStyle,
      HintPopup,
      ChoosePopup,
      ComPopup
    },
    data: function () {
      return {
        id: '',   // id
        headInfo: '',   // 
        items: '',   // 
        baseItem: '',   // 
        isBottom: '',    // 
        swiperOption: {     // 
          notNextTick: true,
          pagination: '.swiper-pagination',
          slidesPerView: 'auto',
          centeredSlides: true,
          paginationClickable: true,
          spaceBetween: 30
        },
        buttonItem: ['', '', '', ''],
        shadowShow: false,    // 
        isHintPopup: false,   // 
        isChoosePopup: false, // 
        isComPopup: false, // 
        popupButtons: '',    // 
        popupName: '',   // 
        itemName: '',   // 
        popupType: ''    // 
      }
    },
    created: function () {
      this.id = this.$route.query.id
      if (this.$route.query.type === 'checkWait') {
        this.isBottom = true
      }
      const data = {
        checkName: '',
        checkInfo: [
          {
            name: '',
            type: 0,
            item: {
              '': '',
              '': '',
              '': '',
              '': '0571-1234567',
              '': '13800001111',
              '': '2017-05-15'
            }
          },
          {
            name: '',
            type: 1,
            item: [
              {
                name: '',
                type: 0,
                item: {
                  '': '3222125936',
                  '': '',
                  '': '',
                  '': '18066668888'
                }
              }
            ]
          },
          {
            name: '',
            type: 0,
            item: {
              '': 'CNY',
              '()': '1,000,000.00',
              '': '',
              '': 'FIXED',
              '(BP)': '0',
              '': '3%',
              '': '3%',
              '': '4%',
              '': '1%',
              'FTP': '',
              '': '2017-05-20',
              '': '2017-05-20',
              '': '2018-01-31',
              '': '2018-01-31',
              '': '256',
              '': '256',
              '': '365',
              '': '',
              '': '',
              '()': '28,054.79',
              '()': '1,028,054.79',
              '': ''
            }
          },
          {
            name: '',
            type: 0,
            item: {
              '': '',
              '': '',
              '': '',
              '': '',
              '': '',
              '': '',
              '': '',
              '': '',
              '': ''
            }
          },
          {
            name: '',
            type: 0,
            item: {
              '': '(25%)',
              '': '1,000,000.00',
              '': '',
              '': '1,000,000.00',
              '()': '1,000,000.00',
              '()': '1,000,000.00',
              '()': '0',
              '(%)': '0'
            }
          },
          {
            name: '',
            type: 0,
            item: {
              '': '',
              '': '',
              '': '10,000,000,000',
              '': '1,000,000.00',
              '': '1,000,000.00',
              '': '1,000,000.00'
            }
          }
        ]
      }
      this.headInfo = {name: data.checkName, isBack: true, iClass: 'fa fa-codepen'}
      this.items = data.checkInfo
      this.baseItem = {id: '00011', itemName: '', initiator: '', initiateBank: '', time: '2017-05-15', tel: '13800001111'}
    },
    mounted: function () {
      $.scrollFix('main')
    },
    methods: {
      // 
      buttonClick: function (name) {
        this.popupType = name
        if (name === '') {
          this.popupName = ''
          this.itemName = ''
          this.popupButtons = ['', '']
          this.isHintPopup = true
        } else if (name === '') {
          this.popupName = ''
          this.itemName = ''
          this.popupButtons = ['', '']
          this.isHintPopup = true
        } else if (name === '') {
          this.popupName = ''
          this.popupButtons = ['', '']
          this.isChoosePopup = true
        } else if (name === '') {
          this.popupName = ''
          this.popupButtons = ['', '']
          this.isComPopup = true
        }
        this.shadowShow = true
      },
      // 
      closePopup: function () {
        this.shadowShow = false
        this.isHintPopup = false
        this.isChoosePopup = false
        this.isComPopup = false
      },
      // 
      hintFun: function (name) {
        if (name === '') {
          this.closePopup()
        } else {
          this.$root.flowFun(this.popupType, this.id, '')
          this.closePopup()
          this.$router.push({ path: '/Check/check-main', query: { isCheckWait: '1'}})
        }
      },
      // 
      comFun: function (name, pop) {
        if (name === '') {
          this.closePopup()
        } else {
          if (pop) {
            this.$root.flowFun(this.popupType, this.id, pop)
            this.closePopup()
            this.$router.push({ path: '/Check/check-main', query: { isCheckWait: '1'}})
          } else {
            this.popupButtons = ['', '']
            this.isComPopup = false
            this.isChoosePopup = true
          }
        }
      },
      // 
      checkFlow: function () {
        this.$router.push({ path: '/Check/check-flow', query: { id: this.id, checkName: this.headInfo.name}})
      }
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus">
  .my-check{
    width inherit
    height 100%
    position relative
    overflow-x hidden
    overflow-y hidden
    background-color #F7F7F7
    .head{
      font-size 0.75rem
    }
    .main {
      background-color #ffffff
      .swiper-container{
        width 100%
        min-height 100%
        overflow-x hidden
        overflow-y hidden
        .title{
          text-align center
          font-size 0.8rem
          height 2rem
          line-height 2rem
          -webkit-box-sizing: border-box
          -moz-box-sizing: border-box
          box-sizing: border-box
        }
        .swiper-slide{
          height 10px
          .content{
            padding  0 0.8rem
            -webkit-box-sizing: border-box
            -moz-box-sizing: border-box
            box-sizing: border-box
            .field{
              font-size 0.7rem
              line-height 1.2rem
              display flex
              &:last-child{
                margin-bottom 1rem
              }
              .key{
                width 5rem
              }
              .value{
                flex 1
                padding-left 0.3rem
                display flex
                align-items center
              }
            }
            .second-title{
              font-size 0.75rem
            }
            .second-field{
              padding-left 0.5rem
              -webkit-box-sizing: border-box
              -moz-box-sizing: border-box
              box-sizing: border-box
            }
          }
        }
        .swiper-slide-active{
          height auto
          overflow-y auto
        }
        .swiper-pagination{
          position fixed
          bottom 0.5rem
          .swiper-pagination-bullet{
            width 16px
            height 16px
          }
        }
        .pag-bottom{
          bottom 2.6rem
        }
      }
    }
    .my-check-main{
      padding-bottom 2.4rem
    }
    .footer{
      position fixed
      bottom 0
      left 0
      width 100%
      height 2.4rem
      background-color #F7F7F7
      z-index 4
      border-top 1px solid #cccccc
      display flex
      justify-content space-between
      align-items center
      padding 0 0.8rem
      -webkit-box-sizing: border-box
      -moz-box-sizing: border-box
      box-sizing: border-box
    }
  }
</style>



